(function ($) {
  'use strict';

  class VideoPlayer {
    constructor(target) {
      this.target = target;
    }

    html() {
      throw new Error('Not implemented');
    }
  }

  class YouTube extends VideoPlayer {
    html() {
      return `<iframe src="https://www.youtube.com/embed/${this.target}?controls=0&autoplay=1"
              frameborder="0" 
              allow="autoplay">
            </iframe>`;
    }
  }

  // $.fn.videoPlayer = function () {
  //   const $element = $(this[0]);

  //   if (!$element.children('iframe').length) {
  //     const target = $element.data('target');
  //     $element.html(`
  //       <iframe src="https://www.youtube.com/embed/${target}?controls=0&autoplay=1" 
  //         frameborder="0" 
  //         allow="autoplay">
  //       </iframe>`
  //     );
  //   }

  //   return this;
  // };

  $.fn.videoPlayer = function () {
    const $element = $(this[0]);

    if (!$element.children().length) {
      const target = $element.data('target');
      $element.html(new YouTube(target).html());
    }

    return this;
  };

}($));
